
:root {
  --red: #f44336;
  --grey-light: #bdbdbd;
  --grey: #878687;
  --grey-dark: #4b4b4b;
  --text: #4b4b4b;
  --blue-light: #00c3e8;
}

body {

  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: #4b4b4b;
  color: var(--text);
  -webkit-font-smoothing: antialiased;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
}

body * {
  box-sizing: inherit;
}

.block {
  min-height: 100vh;
}

@media only screen and (min-width: 992px) {

  .block {
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #3272cf), color-stop(50%, #589dc7), to(#8ab7cb));
    background-image: -webkit-linear-gradient(top, #3272cf 0, #589dc7 50%, #8ab7cb);
    background-image: -o-linear-gradient(top, #3272cf 0, #589dc7 50%, #8ab7cb);
    background-image: linear-gradient(180deg, #3272cf 0, #589dc7 50%, #8ab7cb);
    background-color: #8ab7cb;
    background-repeat: no-repeat;
  }
}

.inner {
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  align-items: flex-start;

}

@media only screen and (min-width: 992px) {

  .inner {
    min-height: calc(100vh - 80px);
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

@-webkit-keyframes shake {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }

  20%,
  40%,
  60%,
  80% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }
}

@keyframes shake {
  from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translate3d(-10px, 0, 0);
            transform: translate3d(-10px, 0, 0);
  }

  20%,
  40%,
  60%,
  80% {
    -webkit-transform: translate3d(10px, 0, 0);
            transform: translate3d(10px, 0, 0);
  }
}

.shake {
  /* animation-name: shake; */
  -webkit-animation: shake .5s linear 1;
          animation: shake .5s linear 1;
}

.formOuter {
  max-width: 400px;
  width: 100%;
  position: relative;
}

@media only screen and (min-width: 992px) {

  .formOuter:before,
  .formOuter:after {
    display: block;
    content: '';
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    background: #fff;
    z-index: -1;
    border-radius: 16px;
    transition: -webkit-transform linear 0.1s;
    transition: transform linear 0.1s;
    transition: transform linear 0.1s, -webkit-transform linear 0.1s;
  }

  .formOuter:before {
    opacity: 0.6;
  }

  .formOuter:after {
    opacity: 0.3;
  }

  .formOuter:hover:before {
    -webkit-transform: translate(-30px, -20px);
            transform: translate(-30px, -20px);
    -webkit-transform: translate(var(--transformXBefore), var(--transformYBefore));
    -webkit-transform: translate(var(--transformXBefore), var(--transformYBefore));
            transform: translate(var(--transformXBefore), var(--transformYBefore));
            transform: translate(var(--transformXBefore), var(--transformYBefore));
  }

  .formOuter:hover:after {
    -webkit-transform: translate(17px, 45px);
            transform: translate(17px, 45px);
    -webkit-transform: translate(var(--transformXAfter), var(--transformYAfter));
    -webkit-transform: translate(var(--transformXAfter), var(--transformYAfter));
            transform: translate(var(--transformXAfter), var(--transformYAfter));
            transform: translate(var(--transformXAfter), var(--transformYAfter));
  }

}


.form {
  width: 100%;
  background: #fff;
  padding: 16px;
  border-radius: 8px;
}


@media only screen and (min-width: 992px) {

  .form {
    box-shadow: 0 1 2px 2px #00c3e8;
    box-shadow: 0 1 2px 2px var(--blue-light);
  }
}

.title {
  font-weight: 100;
  text-align: center;
  margin: 20px 0;
}

.toolbar {
  height: 80px;
  background: #fff;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
}

.row {
  margin-bottom: 16px;
}

label {
  display: block;
  padding-bottom: 8px;
  font-weight: 100;
}

input[type="text"],
input[type="password"] {
  width: 100%;
  font-size: 100%;
  padding: 12px;
  color: #4b4b4b;
  color: var(--grey-dark);
  border-radius: 4px;
  border: 1px solid;
  border-color: #bdbdbd;
  border-color: var(--grey-light);
  outline: 0;
}

input:focus {
  color: #4b4b4b;
  color: var(--grey-dark);
  border-color: #00c3e8;
  border-color: var(--blue-light);
  box-shadow: 0px 0px 1px 1px #00c3e8;
          box-shadow: 0px 0px 1px 1px var(--blue-light);
}

input::-webkit-input-placeholder {
  color: #bdbdbd;
  color: var(--grey-light);
}

input:-ms-input-placeholder {
  color: #bdbdbd;
  color: var(--grey-light);
}

input::-ms-input-placeholder {
  color: #bdbdbd;
  color: var(--grey-light);
}

input::placeholder {
  color: #bdbdbd;
  color: var(--grey-light);
}

.labelErrors {
  color: #f44336;
  color: var(--red);
}

.inputErrors.inputErrors {
  border-color: #f44336;
  border-color: var(--red);
  background-color: rgba(244, 67, 54, 0.1);
  background-color: rgba(244, 67, 54, 0.1);
}

.inputErrors:focus {
    box-shadow: 0px 0px 1px 1px #f44336;
            box-shadow: 0px 0px 1px 1px var(--red);
}

.errors {
  font-size: 12px;
  color: #f44336;
  color: var(--red);
  margin-top: 4px;
}

.submit {
  display: block;
  width: 100%;
  font-size: 100%;
  background: none;
  border-radius: 8px;
  padding: 12px;
  background-color: #00c3e8;
  color: #fff;
  margin-top: 28px;
  cursor: pointer;
  position: relative;
  outline: 0;
  border: 0;
  min-height: 44px;
  transition: background-color linear 0.1s;
}

.submit:hover, .submit:focus {
  background-color: #069ab6;
  /* box-shadow: 0px 0px 1px 1px var(--grey-light); */
}

.separator {
  text-align: center;
  margin-bottom: 16px;
}

.separatorBorder {
  position: relative;
  top: 12px;
  height: 2px;
  background: #bdbdbd;
  background: var(--grey-light);
}

.separatorLabel {
  background: #fff;
  padding: 4px;
  position: relative;
}

.buttonHolder {
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}

.globalError {
  background: #f44336;
  background: var(--red);
  padding: 16px;
  color: #fff;
  margin-bottom: 16px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.25);
}

.globalErrorIcon {
  font-size: 14px;
}

.complete {
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
}

.completeLine {
  display: -ms-flexbox;
  display: flex;
}


